.app-container {
  margin-top: 20px;
  width: 420px; 
  height: 70vh;
  max-width: 100%;
  background-color: #ffffff;
  border-radius: 40px;
  position: relative;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.camera-hole {
  position: absolute;
  top: 20px;
  left: 50%;
  width: 22px;
  height: 22px;
  background-color: black;
  border-radius: 50%;
  transform: translateX(-50%);
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
}

/* Ecranul aplica»õiei */
.screen {
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


h1 {
  font-size: 30px;
  font-weight: normal;
  text-transform: uppercase;
  text-align: center;
  background: linear-gradient(90deg, #3a3a3a, #1f1f1f, #000000);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
  animation: pulseDark 1.008s infinite alternate;
  letter-spacing: 2px;
  margin-bottom: 20px;
  position: relative;
  padding-bottom: 10px;
}


@keyframes pulseDark {
  0% {
    text-shadow: 2px 2px 8px rgb(0, 0, 0);
  }
  100% {
    text-shadow: 2px 2px 6px rgb(59, 101, 240);
  }
}


h1::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -5px;
  width: 100px;
  height: 4px;
  background: linear-gradient(90deg, rgb(59, 101, 240), #3a3a3a, #000000);
  transform: translateX(-50%);
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(59, 101, 240, 0.8);
  animation: underlineGlow 3.5s infinite alternate;
}

@keyframes underlineGlow {
  0% {
    width: 240px;
    box-shadow: 0px 0px 10px rgba(59, 101, 240, 0.5);
  }
  100% {
    width: 420px;
    box-shadow: 0px 0px 20px rgba(59, 101, 240, 1);
  }
}


h2 {
  font-size: 20px;
  font-weight: bold;
  color: #2C2C2C;
  margin-top: 15px;
  margin-bottom: 10px;
}
